:root {
    /*Colors*/
    --primary-color: #eec089;
    --accent-color: #d7d0b6;
    --text-color: #8a9da4;
    --background-color: #4b4d63;
    --header-background: #d7d0b6;
    --footer-background: #4b4d63;
