    --nav-fade-to-color: #4b4d63;

    --section-color-1: rgb(240, 237, 237);
    --serction-color-2: white;

    --blog-border: silver;
    --article-links-secondary: tomato;

    /*Spacing*/
    --spacing-unit: 10px;

    --line-height: 1;
    --double-line-height: 2;

    /* Transitions */
    --transition-duration: 0.5s;

    /* Scale */
    --scale: 1;
    --scale-hover: 1.1;
    --opacity-hover: .9;
    --border-radius: 2px;
    --font-weight: 500;
    --pad10: 10px;
    --pad20: 20px;

    /* Offline font */
    --font-family: Arial, Helvetica, sans-serif;
}

