}

blockquote:before,
blockquote:after {
    position: absolute;
    font-size: 4em;
}

blockquote:before {
    content: open-quote;
    margin-left: -0.6em;
    margin-top: -0.4em;
}

blockquote:after {
    content: close-quote;
    bottom: 0;
    right: 0;
    margin-right: -0.6em;
    margin-bottom: -0.8em;
}

blockquote p {
    display: inline;
