/* Here onward */
nav,
article {
    opacity: var(--opacity-hover);
    background-image: linear-gradient(to bottom right, var(--primary-color), var(--secondary-color));
    padding-bottom: 0;
}

nav#top_navigation {
    justify-content: center;
    background: linear-gradient(to top left, var(--dark-color), var(--light-color));
}

nav#top_navigation ul {
    margin-bottom: 0 !important;
}

nav#top_navigation ul li {
    padding: var(--spacing-unit);

    border-top-left-radius: 1em;
    border-top-right-radius: var(--border-radius);
    margin-right: 1em;
    padding-left: var(--spacing-unit) 10px;
    background-color: silver;
    opacity: 0.9;
    margin-bottom: 0;
    background-image: linear-gradient(to top left, var(--primary-color), rgb(161, 212, 230));
}

nav#top_navigation ul li a {
    font-family: var(--font-family);
    font-weight: var(--font-weight);
    text-transform: capitalize !important;
}

nav#top_navigation ul li:hover {
    opacity: var(--opacity-hover);
    background-image: linear-gradient(to top left, var(--primary-color), rgb(161, 212, 230));
}

nav#top_navigation ul li:first {
    border: 1px solid rgb(128, 128, 128);
    background-color: darkgrey;
}

